.App {
  min-height: 100vh;
  text-align: center;

  background-image: linear-gradient(to right, #ff8177 0%, #ff867a 0%, #ff8c7f 21%, #f99185 52%, #cf556c 78%, #b12a5b 100%);
}
.Search_form{
  min-height: 20vh;
  display: flex;
  justify-content: center;
  align-items:center;
}

.search_bar{
  width:50%;
  border: none;
  padding: 10px;
  height: 15%;
}

.navigation_bar{
  position: top;
  height: 50px;
  top: 0px;
  z-index: 2;
  /* height: 15%; */
  display: block;
  background: scroll;
  background: #282c34;
}

.search_button{
  background: greenyellow;
  border: none ;
  padding: 10px 20px;
  color: white;
}

.recipes{
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

